import {
  updateSliderHTML,
  updateScoreDOM,
  toggleStartBtn,
  resetGameBoard,
  displayElement,
  pauseDots
} from "./util";
import { addDot } from "./dot";
import { startTimer, clearTimer } from "./timer";
import {
  SMALL_VIEWPORT,
  INITIAL_SIZE,
  INITIAL_SLIDER_VALUE,
  NEW_DOT_INTERVAL,
  isCompetition,
  winningConfig
} from "./constant";

import instance from "./state";

const dotsGameInit = function (config = {}) {
  // DOM Elements
  const $slider = document.getElementById("speedControl");
  const $startButton = document.getElementById("startBtn");
  const $resetButton = document.getElementById("clearBtn");
  const $modeSelectionDD = document.querySelector(".mode__selection");
  const $changeMode = document.getElementById("changeMode");

  //Default config of the game and can be overwritten by config passed by user
  const defaultConfig = {
    INITIAL_SIZE,
    INITIAL_SLIDER_VALUE,
    NEW_DOT_INTERVAL,
    isCompetition,
    winningConfig
  };

  // Overwritting the default config by user config
  config = { ...defaultConfig, ...config };

  //Initial State
  // let gameInitialState = {
  //   currentSpeed: $slider.value,
  //   score: 0,
  //   isPlaying: false
  // };

  let { state: initialState, setProperty, getProperty } = instance;

  let myInterval;

  //Default behavior when the game loads initialy
  updateSliderHTML(getProperty("currentSpeed"));
  updateScoreDOM(0);
  if (window.innerWidth > SMALL_VIEWPORT) {
    displayElement($modeSelectionDD);
  }

  //Attaching event listener to the objects

  // Update currentSpeed when slider is adjusted
  $slider.addEventListener("input", (event) => {
    const { value } = event.target;
    setProperty("currentSpeed", value);
    //gameInitialState.currentSpeed = value;
    updateSliderHTML(value);
  });

  // Capture start/pause lick
  $startButton.addEventListener("click", () => {
    let isPlaying = getProperty("isPlaying");
    setProperty("isPlaying", !isPlaying);
    toggleStartBtn(getProperty("isPlaying"));
    // const { isPlaying } = gameInitialState;
    // gameInitialState.isPlaying = !isPlaying;
    //toggleStartBtn(gameInitialState.isPlaying);

    // Add a dot to a random spot every second
    //if (gameInitialState.isPlaying) {
    if (getProperty("isPlaying")) {
      myInterval = setInterval(
        () => addDot(instance._data, config.INITIAL_SIZE),
        config.NEW_DOT_INTERVAL
      );
      if (config.isCompetition) {
        startTimer(config.winningConfig);
      }
    } else {
      clearTimer(myInterval);
    }

    // stop the animation for all dots on the screen
    pauseDots(getProperty("isPlaying"));
  });

  // Reset game
  $resetButton.addEventListener("click", () => {
    resetGameBoard(myInterval, initialState);
  });

  // This only works for desktop viewport to change the mode of the game
  $changeMode.addEventListener("change", (event) => {
    const { value } = event.target;
    resetGameBoard(myInterval, initialState);
    config.isCompetition = value === "competition" ? true : false;
  });
};

//Game intializer with the config passed from the user
dotsGameInit({ isCompetition: false });
